import axios from 'axios';
import ConfigService from './ConfigService';

export default class ChainService {
  constructor(network) {
    this.confg = new ConfigService();
    this.network = this.confg.network;
  }

  getTableUrl() {
    const url = `${this.network.protocol}://
                 ${this.network.host}: 
                 ${this.network.port}/v1/chain/get_table_rows`;
    return url;    
  }

  async readTable(code, table) {
    return await axios.post(this.getTableUrl(), {
        code: code,
        table: table
      }
    );
  }
}